.close-modal{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal-content {
  position: absolute;
  background-color: #fefefe;
  padding: 1rem;
  border-radius: 0.5rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media(max-width:768px){
  .modal-content{
    width: 75%;
  }
}
@media(max-width:450px){
  .modal-content{
    width: auto;
    left: 0;
    transform: translate(0, -50%);
    border-radius: 0;
  }
}
h1{
  text-align: center;
}
.open-modal {
  position: fixed;
  padding: 0.5rem 0.8rem;
  top: 1rem;
  right: 6rem;
  border-radius: 50%;
  border: 1px solid #ccc;
}

